.hidden {
    display: none;
}
.boxes label:first-of-type {
    margin-right: 15.98px;
}
.boxes label {
    display: block;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    padding: 19px 22.17px;
    border: 1px solid #E8E8E8;
    border-radius: 4px;
    cursor: pointer;
    min-height: 110px;
    width: 307px;
    word-wrap: break-word;
    -webkit-transition: border ease-in-out 0.3s;
    -o-transition: border ease-in-out 0.3s;
    transition: border ease-in-out 0.3s;
}
 .boxes {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-bottom: 3rem;
}

.boxes label.checked {
    border: 1px solid #ff80ab;
}

.boxes label .title {
    font-size: 15px;
    line-height: 19px;
    color: #000;
    margin-bottom: 5px;
}

.boxes label .description {
    color: #999999;
    font-size: 14px;
    line-height: 19px;
}

.form-element, .field-input, .react-dadata__input {
    padding: 22.99px 16.42px 25.32px;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 17px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%;
    color: #A4A6A6;
    border: 1px solid #E8E8E8;
}
.form-element.error, .field-input.error {
    border-color: #3e29ff;
    color: #3e29ff;
}
.field-input-box label {
    position: absolute;
    color: #A4A6A6;
    font-size: 16px;
    line-height: 24px;
    left: 16.42px;
    top: 22.99px;
    cursor: pointer;
}
.field-input:focus, .field-input:hover {
    border: 1px solid #3e29ff;
}


.field-input:focus, .field-input:hover {
    border: 1px solid #3e29ff;
}
.form-element:active, .form-element:hover, .form-element:focus, .field-input:active, .field-input:hover, .field-input:focus, .react-dadata__input {
    outline: 0;
    outline-offset: 0;
}
.react-dadata__input:focus {
    border-color: #E8E8E8;
    box-shadow: none;
}
.react-dadata__input {
    border-radius: 0;
    height: auto;
}